.editContainer {
    background: rgba(255, 255, 255, 0.2);
    margin: 5px 0px;
    padding: 5px;
    position: relative;
    -webkit-animation: showAnimation 1.5s;
            animation: showAnimation 1.5s;
}

.showEdit {
    position: absolute;
    left: 11px;
    top: 6px;    
    opacity: 0.3;
    cursor: pointer;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;  
    color: black;
    font-size: 18px;  
}

.showEdit:hover {
    opacity: 1;
}

.showEditOk {
    position: absolute;
    left: 9px;
    top: 7px;    
    opacity: 0.3;
    cursor: pointer;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;  
    color: black;
    font-size: 18px;  
}

.showEditOk:hover {
    opacity: 1;
}


