.add {
    position: absolute;
    left: 5px;
    top: 6px;
    width: 32px;
    height: 32px;
    opacity: 0.3;
    cursor: pointer;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;    
}

.add:hover {
    opacity: 1;    
}

.add:before, .add:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 22px;
    width: 2px;
    background-color: rgb(25, 160, 25);    
}

.add:before {
    -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
            transform: rotate(0deg);
}

.add:after {
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}

.addContainer {
    background: rgba(255, 255, 255, 0.2);
    margin: 5px 30px;
    padding: 5px 10px;
    position: relative;
    -webkit-animation: showAnimation 1.5s;
            animation: showAnimation 1.5s;
}

input[type=text] {
    font-size: 16px;
    background: none;
    outline: none;
    border: none;    
    margin-left: 30px;
    margin-right: 0px;    
    width: 98%;    
}


