.lists {
    position: absolute;
    height: 100%;
    width: 100%;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(186,151,128,0.6)), color-stop(77%, rgba(203,143,100,0)));
    background: -webkit-linear-gradient(top, rgba(186,151,128,0.6) 0%, rgba(203,143,100,0) 77%);
    background: -o-linear-gradient(top, rgba(186,151,128,0.6) 0%, rgba(203,143,100,0) 77%);
    background: linear-gradient(180deg, rgba(186,151,128,0.6) 0%, rgba(203,143,100,0) 77%);
}

.list {    
    background: rgba(255, 255, 255, 0.2);   
    margin: 5px 30px;
    padding: 5px 10px;
    position: relative;
    -webkit-animation: showAnimation 1.5s;
            animation: showAnimation 1.5s;    
}

.name {
    margin-left: 30px;
    font-size: 18px;    
}

@-webkit-keyframes showAnimation {
    0%   { opacity: 0; }
    100% { opacity: 1; }
}

@keyframes showAnimation {
    0%   { opacity: 0; }
    100% { opacity: 1; }
}


