.item {    
    border-bottom: 1px solid white;
    margin: 5px 30px;
    padding: 5px 10px;
    position: relative;
    -webkit-animation: showAnimation 1.5s;
            animation: showAnimation 1.5s; 
    z-index: 1;
}

.itemName {
    margin-left: 30px;
}

.show {
    position: absolute;
    left: 10px;
    top: 8px;    
    opacity: 0.3;
    cursor: pointer;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;  
    color: black;
    font-size: 18px;  
}

.show:hover {
    opacity: 1;
}

@-webkit-keyframes showAnimation {
    0%   { opacity: 0; }
    100% { opacity: 1; }
}

@keyframes showAnimation {
    0%   { opacity: 0; }
    100% { opacity: 1; }
}


